.zoehis_time_editor{
  .zoehis_input_icon{color: $FONTAUXILIARY;}
  &.zoehis_over .zoehis_input_icon{color: $MAINCOLOR;}
}
.zoehis_time_panel{
  font-family: $FONTARIAL,$FONTSUN; outline: none;
  font-size: $FONTMAINS; color: $FONTMAIN;
  box-shadow: $SHADOWVALUE; margin: 3px 0; box-sizing: border-box; background-color: $MAINBG;
}
.zoehis_time_panel__title{
  overflow: hidden;
  .zoehis_time_panel__title-inner{width: calc(50% - 19px); padding-right: 12px; padding-left: 6px; float: left; color: $FONTRPTITLE;
    line-height: 30px;
    border-bottom: 1px solid #f4f4f4;
    text-align: center;
  }
  .zoehis_time_panel__title-inner+.zoehis_time_panel__title-inner{border-left: 1px solid #f4f4f4;}
  .seconds_title{display: none;}
  &.has_seconds{
    .seconds_title{display: block;}
    .zoehis_time_panel__title-inner{width: calc(33% - 19px);}
  }
}
.zoehis_time_panel__content{
  font-size: 0; position: relative; overflow: hidden;
  //&:before,&:after{
  //  content: ":"; top: 50%; color: $FONTWHITE; position: absolute; font-size: $FONTMAINS;
  //  margin-top: -17px; line-height: 16px; background-color: $MAINBG; height: 32px;
  //  z-index: -1; left: 0; right: 0; box-sizing: border-box; padding-top: 6px; text-align: left;
  //}
  //&:before{
  //  padding-left: 50%;
  //  margin-right: -2px;
  //}
  //&:after{
  //  left: 50%;
  //  margin-left: -2px;
  //}
  &.has_seconds{
    &:before{ padding-left: 28%; }
    &:after{ left: 63%; }
  }
}
.zoehis_time_panel__footer{
  border-top: 1px solid $MAINBORDERLINES; padding: 4px; height: 36px; line-height: 25px; text-align: right; box-sizing: border-box;
}
.zoehis_time_panel__btn{
  display: inline-block;
  border: none; line-height: 28px; padding: 0 5px; margin: 0 5px; font-family: $FONTARIAL,$FONTSUN;
  cursor: pointer; background-color: transparent; outline: none; font-size: $FONTMAINS; color: $MAINCOLOR;
}

//zoehis_time_spinner
.zoehis_time_spinner{
  width: 100%; white-space: nowrap;
  &.has_seconds .zoehis_time_spinner__wrapper{
    width: calc(33% - 7px);
  }
}
.zoehis_time_spinner__wrapper{
  max-height: 162px; overflow: auto; display: inline-block; padding-left: 6px; width: calc(50% - 7px); vertical-align: top; position: relative;
  .zoehis_scrollbar__wrap{padding-bottom: 15px;}
  &:hover{background-color: #f5fcff;}
}
.zoehis_time_spinner__wrapper + .zoehis_time_spinner__wrapper{border-left: 1px solid #f4f4f4;}
.zoehis_time_spinner__list{
  padding: 0; margin: 0; list-style: none; text-align: center;
  &:before,&:after{
    content: ""; display: block; width: 100%; height: 64px;
  }
}
.zoehis_time_spinner__item{
  height: 24px; line-height: 24px; font-size: 14px; margin-top: 8px;
  &:hover:not(.active){background-color: $MAINCOLOR; cursor: pointer; color: $FONTWHITE;}
  &.active{color: $MAINCOLOR; cursor: default;border-bottom: 1px solid $MAINCOLOR;}
  &.disabled{color: $FONTAUXILIARY; cursor: default;}
}


